import { useEffect, useState } from 'react'
import Icon from '../../Common/Icon'

import { Button } from '../Controls'
import DemoHelpDialog from './Dialogs/DemoHelpDialog'
import styles from './style.module.css'

function DemoPicker ({ onSelect }) {
  const [showDemoHelpDialog, setShowDemoHelpDialog] = useState(false)

  useEffect(() => {
    (async function () {
      const { layout, keymap } = await import('./data/demo.json')
      onSelect({ source: 'demo', layout, keymap })
    })()
  }, [onSelect])

  return (
    <>
      <Button
        style={{ background: 'none', border: 'none' }}
        onClick={() => setShowDemoHelpDialog(true)}
      >
        <Icon
          name="info"
          className={styles.help}
        /> What is this?
      </Button>
      {showDemoHelpDialog && (
        <DemoHelpDialog onDismiss={() => setShowDemoHelpDialog(false)} />
      )}
    </>
  )
}

export default DemoPicker
