import Link from '../../../Common/Link'
import DialogBox from '../../../Common/DialogBox'
import Modal from '../../../Common/Modal'

export default function DemoHelpDialog (props) {
  const demoRepoUrl = 'https://github.com/nickcoutsos/keymap-editor-demo-crkbd/'
  const templateUrl = 'https://github.com/nickcoutsos/keymap-editor-demo-crkbd/generate'
  const demoLink = <Link href={demoRepoUrl} external>GitHub Repo</Link>

  return (
    <Modal>
      <DialogBox {...props}>
        <h2>Keymap Editor Demo</h2>
        <p>
          Welcome! This demo keyboard is provided to let you experiment with the
          keymap editor before setting up your own repository.
        </p>
        <h3>Features</h3>
        <ul>
          <li>
            <strong>WYSIWYG Editing</strong>. Just click on key and behaviour
            bindings to make changes, add or remove layers in the sidebar.
          </li>
          <li>
            <strong>Keymap generation</strong>. The editor can export keymap to
            ZMK's syntax (when running locally or integrated with GitHub), and
            will even attempt to format the <code>.keymap</code> file for better
            readability.
          </li>
          <li>
            <strong>Devicetree parsing</strong>. <em>(Experimental!)</em> Read
            and write to your existing ZMK <code>.keymap</code> file so that you
            can still use ZMK features that the app doesn't support yet.
          </li>
        </ul>
        <h3>What's next?</h3>
        <p>
          If you like the editor, try integrating with your own GitHub hosted
          keyboard config. <Link href={templateUrl} external>Use it as a
          template</Link> for your own, or just read through the {demoLink} for
          this keyboard to learn what to apply to your existing keyboard.
        </p>
      </DialogBox>
    </Modal>
  )
}
